"use client";
import { useState, useEffect } from "react";
import { BiSearch, BiMailSend } from "react-icons/bi";
import { FiMail, FiPhone, FiMessageSquare } from "react-icons/fi";
import { adminService } from "@/services/adminService";
import toast from "react-hot-toast";

export default function WebsiteQueriesTable() {
  const [queries, setQueries] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    fetchQueries();
  }, []);

  const fetchQueries = async () => {
    setIsLoading(true);
    try {
      const response = await adminService.getAllContactQueries({
        page: 1,
        limit: 100 // Fetch more to show all queries
      });
      console.log("Contact queries response:", response);
      // Backend returns: { success: true, data: [...], pagination: {...} }
      const queriesData = response?.data || response?.queries || [];
      console.log("Contact queries loaded:", queriesData.length);
      console.log("Sample query:", queriesData[0]);
      setQueries(Array.isArray(queriesData) ? queriesData : []);
    } catch (error) {
      console.error('Failed to load queries:', error);
      console.error('Error details:', error.response?.data);
      toast.error(error.response?.data?.message || 'Failed to load contact queries');
      setQueries([]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleMarkAsResolved = async (queryId) => {
    try {
      await adminService.updateQueryStatus(queryId, { status: 'resolved' });
      toast.success('Query marked as resolved');
      fetchQueries(); // Refresh the list
    } catch (error) {
      console.error('Failed to update query:', error);
      toast.error('Failed to update query status');
    }
  };

  // Filter queries based on search term
  const filteredQueries = queries.filter(query =>
    query.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    query.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    query.phone?.includes(searchTerm) ||
    query.subject?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    query.message?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const totalPages = Math.ceil(filteredQueries.length / entriesPerPage);

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Contact Queries</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li><a href="/admin/queries" className="hover:underline text-gray-600">Contact Queries</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">All Contact Queries</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border p-6">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Website Contact Queries Table</h2>
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-2">
            <select 
              value={entriesPerPage} 
              onChange={(e) => {
                setEntriesPerPage(Number(e.target.value));
                setCurrentPage(1);
              }} 
              className="border rounded-lg px-3 py-2 text-sm"
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
            <span className="text-sm text-green-600 font-medium">entries per page</span>
          </div>
          <div className="relative">
            <input 
              type="text" 
              placeholder="Search..." 
              value={searchTerm} 
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setCurrentPage(1);
              }} 
              className="border rounded-lg pl-4 pr-10 py-2 text-sm w-64"
            />
            <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="bg-gray-50 border-b">
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Name</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Mobile Number</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Subject</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Message</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Status</th>
                <th className="px-4 py-3 text-left text-sm font-semibold text-green-600">Action</th>
              </tr>
            </thead>
            <tbody>
              {isLoading ? (
                <tr>
                  <td colSpan="6" className="px-4 py-12 text-center text-gray-500">
                    Loading queries...
                  </td>
                </tr>
              ) : filteredQueries.length > 0 ? (
                filteredQueries.slice((currentPage - 1) * entriesPerPage, currentPage * entriesPerPage).map((query, idx) => (
                  <tr key={query._id || idx} className={`border-b hover:bg-gray-50 transition-colors ${idx % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'}`}>
                    <td className="px-4 py-3 text-sm">
                      <div className="flex items-center gap-2">
                        <FiMail className="text-gray-400" />
                        <div>
                          <div className="font-medium text-gray-900">{query.name || 'N/A'}</div>
                          <div className="text-xs text-gray-500">{query.email || 'N/A'}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-4 py-3 text-sm">
                      <div className="flex items-center gap-1">
                        <FiPhone className="text-gray-400" size={14} />
                        {query.phone || 'N/A'}
                      </div>
                    </td>
                    <td className="px-4 py-3 text-sm font-medium text-gray-900">{query.subject || 'N/A'}</td>
                    <td className="px-4 py-3 text-sm max-w-md">
                      <div className="line-clamp-2 text-gray-700">{query.message || 'N/A'}</div>
                    </td>
                    <td className="px-4 py-3">
                      <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                        query.status === 'resolved' ? 'bg-green-100 text-green-700' :
                        query.status === 'in-progress' ? 'bg-blue-100 text-blue-700' :
                        'bg-yellow-100 text-yellow-700'
                      }`}>
                        {query.status ? query.status.replace('-', ' ').toUpperCase() : 'PENDING'}
                      </span>
                    </td>
                    <td className="px-4 py-3">
                      <button 
                        onClick={() => handleMarkAsResolved(query._id)}
                        className="px-4 py-2 bg-green-700 text-white text-sm rounded-lg hover:bg-green-800 transition disabled:opacity-50 disabled:cursor-not-allowed"
                        disabled={query.status === 'resolved'}
                      >
                        {query.status === 'resolved' ? 'Resolved' : 'Mark As Fixed'}
                      </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="6" className="px-4 py-12 text-center text-gray-500">
                    <div className="text-4xl mb-2">ðŸ“­</div>
                    {isLoading ? 'Loading queries...' : searchTerm ? 'No queries match your search' : 'No contact queries found'}
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        {totalPages > 1 && (
          <div className="flex items-center justify-between mt-6 pt-4 border-t border-gray-200">
            <div className="text-sm text-gray-600">
              Showing {((currentPage - 1) * entriesPerPage) + 1} to {Math.min(currentPage * entriesPerPage, filteredQueries.length)} of {filteredQueries.length} entries
            </div>
            <div className="flex items-center gap-2">
              <button
                onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                disabled={currentPage === 1}
                className="px-3 py-1 border border-gray-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50"
              >
                Previous
              </button>
              <span className="text-sm text-gray-600">
                Page {currentPage} of {totalPages}
              </span>
              <button
                onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                disabled={currentPage === totalPages}
                className="px-3 py-1 border border-gray-300 rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed hover:bg-gray-50"
              >
                Next
              </button>
            </div>
          </div>
        )}
      </div>
    </>
  );
}



